.header.white {
  &,
  .menu__link,
  .auth__login,
  .auth__signup {
    color: var(--white-color);
  }
}

.header.dark {
  &,
  .menu__link,
  .auth__login,
  .auth__signup {
    color: var(--blue-color);
  }
}

.header {
  position: absolute;
  top: 41px;
  left: 0;
  width: 100%;
}

.header__container {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.header__navbar {
  display: flex;
  align-items: center;
}

.header__logo {
  margin-right: 38px;
}

.logo {
  display: flex;
  align-items: center;
}

.logo__main {
  position: relative;
  top: -5px;
  margin-right: 18px;
  font-weight: 700;
  font-size: 35px;
  line-height: 44px;
  color: var(--white-color);
  text-decoration: none;
}

.logo__descr {
  font-weight: 500;
  font-size: 10px;
  line-height: 100%;
  letter-spacing: -0.01em;
}

.header__line {
  margin-right: 51px;
  width: 1px;
  height: 59px;
  background-color: var(--white-color);
}

.header__menu {
  margin-right: 117px;
}

.menu__list {
  min-width: 394px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.menu__item {
  position: relative;
  list-style-type: none;

  &::after {
    content: "";
    position: absolute;
    bottom: -3px;
    left: 0;
    width: 100%;
    border: 1px solid var(--white-color);
    visibility: hidden;
    opacity: 0;
    transition: var(--transition);
  }

  &:hover::after {
    visibility: visible;
    opacity: 1;
    transform: translateY(4px);
  }
}

.menu__link {
  font-weight: 400;
  font-size: 21px;
  line-height: 26px;
  letter-spacing: -0.01em;
  color: var(--white-color);
  text-decoration: none;
}

.lang {
  .svg-image-shevron {
    transition: var(--transition);
  }

  &.active {
    .svg-image-shevron {
      transform: scaleY(-1);
    }
  }
}

.lang__selected {
  display: flex;
  align-items: center;
  gap: 0 12px;
  cursor: pointer;
}

.lang__value {
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  letter-spacing: -0.01em;
  color: var(--white-color);
}

.lang__icon {
  width: 29px;
  height: 29px;

  img {
    display: block;
    width: 100%;
    height: auto;
    border-radius: 50%;
  }
}

.lang__dropdown {
  display: none;
}

.auth {
  display: flex;
  align-items: flex-end;
}

.auth__login,
.auth__signup {
  display: flex;
  align-items: flex-end;
  height: 68px;
  margin-top: 9px;
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  letter-spacing: -0.01em;
  color: var(--white-color);
  text-decoration: none;
  transition: var(--transition);
}

.auth__login {
  margin-right: 20px;
  width: 145px;
  padding: 24px 0 18px 0;
  justify-content: center;

  &:hover {
    background-color: var(--blue-color);
    border-radius: 6px;
  }
}

.auth__signup {
  width: 188px;
  padding: 24px 30px 18px 41px;
  border: 1px solid rgba(255, 255, 255, 0.6);

  .icon__arrow {
    display: inline-block;
    margin-left: 27px;
    transform: rotate(-45deg);
    transition: var(--transition);
  }

  &:hover {
    background-color: var(--blue-color);
    border: 1px solid var(--blue-color);
    border-radius: 6px;
  }

  &:hover .icon__arrow {
    transform: rotate(0);
  }
}
